<template><section><h2>DatePicker 日期选择器</h2>
<h3>基础使用</h3>
<p>基础的日期选择器用法</p>
<coding title="基础使用" :code="baseCode">
  <ru-date-picker></ru-date-picker>
</coding>
<h3>带有快捷键的选择器</h3>
<coding title="快捷选项" note="日期选择器可以设置快捷选项" :code="optionCode">
  <ru-date-picker showOptions=""></ru-date-picker>
</coding>
<h3>options</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>showOptions</td>
<td>是否使用快捷键</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data () {
    return {
      baseCode: '<ru-date-picker></ru-date-picker>',
      optionCode: '<ru-date-picker showOptions></ru-date-picker>'
    }
  }
}
</script>


